<template>
  <div class="footer" :style="'color: '+color">
    <p><span @click.prevent="_linkS()">©{{ msg }}</span> 一只充满“智慧”的狼</p>
  </div>
</template>

<script>
import { ElMessage } from 'element-plus'
export default {
  data() {
    return {
      msg: "",
      color:"white",
      linkS: false
    }
  },
  props: {
    msg: String,
    linkS: Boolean,
    color:String
  },
  methods: {
    _linkS() {
      if (this.linkS == "true") {
        this._Msg();
      }
      if (this.linkS == "false") {
        this._link();
      }
    },
    _Msg() {
      ElMessage({
        message: '站点维护中',
        type: 'warning',
      })
    },
    _link() {
      location.href = "https://alwolf.cn"
    }
  }
}
</script>

